// select consultation block

const selectVisaBtn = document.querySelector('.main__consultation--form--visaType--select--btn');
const visaList = document.querySelector('.main__consultation--form--visaType--select--list');
const visaListItem = document.querySelectorAll('.main__consultation--form--visaType--select--list--item');
const selectListVisibility = 'main__consultation--form--visaType--select--list--visible';
const selectVisaBtnActive = 'main__consultation--form--visaType--select--btn--active';
const selectInputHidden = document.querySelector('.main__consultation--form--visaType--select--inputHidden');
const numberInput = document.querySelector('.main__consultation--form--number--input');
const nameInput = document.querySelector('.main__consultation--form--name--input');
const inputActive = 'main__consultation--form--name--input--active';

let typeActive = false;

nameInput.addEventListener('click', function() {
  this.classList.add(inputActive);
});

numberInput.addEventListener('click', function() {
  this.classList.add(inputActive);
});

selectVisaBtn.addEventListener('click', function() {
  visaList.classList.toggle(selectListVisibility);
  this.classList.add(selectVisaBtnActive);
});

visaListItem.forEach((listItem) => {
  listItem.addEventListener('click', function(e) {
    e.stopPropagation();
    selectVisaBtn.innerText = this.innerText;
    selectVisaBtn.focus();
    selectInputHidden.value = this.dataset.value;
    visaList.classList.remove(selectListVisibility);
    typeActive = true;
  });
});

document.addEventListener('click', (e) => {
  if (e.target !== nameInput && nameInput.value == "") {
    nameInput.classList.remove(inputActive);
  }

  if (e.target !== numberInput && numberInput.value == "") {
    numberInput.classList.remove(inputActive);
  }

  if (e.target !== selectVisaBtn && typeActive === false) {
    selectVisaBtn.classList.remove(selectVisaBtnActive);
    visaList.classList.remove(selectListVisibility);
  } else if (e.target !== selectVisaBtn && typeActive === true) {
    visaList.classList.remove(selectListVisibility);
  }
});

document.addEventListener('keydown', (e) => {
  if (e.key === 'Escape' && selectInputHidden.value === "0" || e.key === 'Tab' && selectInputHidden.value === "0") {
    selectVisaBtn.classList.remove(selectVisaBtnActive);
    visaList.classList.remove(selectListVisibility);
  }
  
  if (e.key === 'Tab' && nameInput.value != "") {
    numberInput.classList.add(inputActive);
  } else if (e.key === 'Tab' && nameInput.value == "") {
    nameInput.classList.remove(inputActive);
    numberInput.classList.add(inputActive);
  }
  if (e.key === 'Tab' && numberInput.value == "") {
    numberInput.classList.remove(inputActive);
  }
});

window.addEventListener('scroll', function() {
  if (this.scrollY > 50) {
    document.querySelector('.header').classList.add('sticky');
  } else {
    document.querySelector('.header').classList.remove('sticky');
  }
});


document.addEventListener('DOMContentLoaded', function() {
  const form = document.getElementById('contactUs--form');

  form.addEventListener('submit', formSend);

  async function formSend(e) {
    e.preventDefault();

    let error = formValidate(form);

    let formData = new FormData(form);
    

    if (error === 0) {
      let response = await fetch('sendmail.php', {
        method: 'POST',
        body: formData
      });
      if (response.ok) {
        let result = await response.json();
        alert(result.message);
        form.reset();
      } else {
        alert("Ошибка, не отправлено");
      }
    } else {
      alert(`Заполните обязательные поля, ошибок: ${error}`);
    }

    // formValidate(form);

    // let formData = new FormData(form);

    // let response = await fetch('sendmai.php', {
    //   method: 'POST',
    //   body: formData
    // });

    // if (response.ok) {
    //   let result = await response.json();
    //   alert(result.message);
    //   form.reset();
    // } else {
    //   alert("Error");
    // }

  }

  function formValidate() {
    let error = 0;
    let formReq = document.querySelectorAll('._req');

    for (let index = 0; index < formReq.length; index++) {
      const input = formReq[index];
      formRemoveError(input);

      // if (input.classList.contains('_name')) {

      // }

      if (input.value === '') {
        formAddError(input);
        error++;
      } else if (input.classList.contains('_email')) {
        if (emailTest(input) === false) {
          formAddError(input);
          error++;
        }
      }
    }

    return error;
  }

  function formAddError(input) {
    input.classList.add('_error');
  }

  function formRemoveError(input) {
    input.classList.remove('_error');
  }

  function emailTest(input) {
    return /\S+@\S+\.\S+/.test(input.value);
  }
});
